.container {
  background-color: var(--color-1);
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 40px;
  padding-bottom: 40px;
  color: #fff;
}
.title {
  font-size: 35px;
  font-weight: 600;
  margin-bottom: 10px;
}

/* ------------ SWIPER -------------------------- */
.slider_wrapper {
  position: relative;
}
.swiper_wrappe {
  padding-bottom: 40px;
}
.img_wrapper {
  width: 100%;
  height: 360px;
  /* aspect-ratio: 4 / 3; або 16/9 */
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
  overflow: hidden;
}

.img_wrapper img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  display: block;
}

.swiper_button_prev,
.swiper_button_next {
  position: absolute;
  top: calc(50% - 30px);
  transform: translateY(-50%);
  z-index: 10;
  background-color: #00000013;
  color: #fff;
  padding: 12px 16px;
  /* border-radius: 50%; */
  cursor: pointer;
  font-size: 18px;
  user-select: none;
  transition: background 0.3s ease;
}

.swiper_button_prev:hover,
.swiper_button_next:hover {
  background-color: #0000002a;
}

.swiper_button_prev {
  left: 5px;
}

.swiper_button_next {
  right: 5px;
}

/* ------------ SWIPER -------------------------- */

@media (max-width: 992px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media (max-width: 768px) {
  .title {
    font-size: 25px;
  }
}

@media (pointer: coarse) {
  .swiper_button_prev,
  .swiper_button_next {
    display: none;
  }
}
