.container {
  background-color: var(--color-2);
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 40px;
  padding-bottom: 400px;
  font-size: 22px;
}
.title {
  font-size: 35px;
  font-weight: 600;
  margin-bottom: 10px;
}

/* --------------------------------------------------------------- */
.accordion {
  font-family: sans-serif;
}

.accordion_item {
  border-bottom: 1px solid #ddd;

  /* border-bottom: 1px solid var(--color-3); */
}

.accordion_header {
  width: 100%;
  text-align: left;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 0.7rem;
  padding-bottom: 0.7rem;
  /* background: #f5f5f5; */
  background: var(--color-1);
  border-left: 1px solid var(--color-1);
  border-right: 1px solid var(--color-1);
  color: #fff;
  border: none;
  outline: none;
  font-size: 1.1rem;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.accordion_content {
  overflow: hidden;
  max-height: 0;
  transition: max-height 0.3s ease;
  border-left: 1px solid #ddd;
  border-right: 1px solid #ddd;
}

.content_inner {
  padding: 1rem;
  background: #fff;
}

.arrow {
  font-size: 1.5rem;
  transition: transform 0.3s ease;
}

.accordion_item.open .arrow {
  transform: rotate(180deg);
}

/* --------------------------------------------------------------- */

@media (max-width: 992px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media (max-width: 768px) {
  .container {
    font-size: 16px;
  }
  .title {
    font-size: 25px;
  }
}
